import styled from 'styled-components';

export default styled.h1`
  color: ${p => p.theme.color.primary};
  text-align: center;
  width: 100%;
  margin-top: 0;
  margin-bottom: 0.2em;
  letter-spacing: 20px;
  font-size: 6em;
  opacity: 0.8;
  z-index: 0;
  font-weight: 400;
`;



